<template lang="html">
<div>
    <ul class="nav nav-sidebar" v-for="list in nodeTree">
        <li class="pl10"><h5>{{list.title}}</h5></li>
        <li v-for="{path, title, isDev} in list.child" :class="{active: $route.path === `/${path}`}">
            <router-link :to="{path}">{{title}} <span class="glyphicon glyphicon-wrench" v-if="isDev" style="font-size:12px;"></span></router-link>
        </li>
    </ul>
</div></template>

<script>
export default {
    data() {
        return {
            nodeTree: [{
                title: '',
                child: [
                    { path: 'overview', title: '' },
                    { path: 'principle', title: '' }
                ]
            },
            {
                title: '',
                child: [
                    { path: 'text', title: 'Copywriting ' },
                    { path: 'font', title: 'Typography ' },
                    { path: 'color', title: 'Color ' },
                    { path: 'space', title: 'Space ' },
                    { path: 'other', title: '' }
                ]
            },
            {
                title: '',
                child: [
                    // { path: 'designstyle', title: '' },
                    { path: 'base', title: 'Layout ' },
                    { path: 'button', title: 'Button ' }
                    
                ]
            },
            {
                title: '',
                child: [
                    // { path: 'modal', title: '' },
                    { path: 'table', title: 'Table ' },
                    // { path: 'search', title: 'search ' },
                    { path: 'autocomplete', title: 'Autocomplete ' },
                    { path: 'pager', title: 'Page ' },
                    { path: 'minimsg', title: 'Minimsg ', isDev: true},
                    { path: 'datepicker', title: 'Date ' },
                    { path: 'slideout', title: 'Slideout ' },
                    { path: 'loading', title: 'Loading ' },
                    { path: 'transfers', title: 'Transfers ' },
                    { path: 'area', title: 'Area-selector ' },
                    { path: 'uploader', title: 'Uploader ', isDev: true},
                    { path: 'textarea', title: 'Textarea '}
                ]
            },
            {
                title: '',
                child: [
                    { path: 'template1', title: '1' }
                ]
            }
            ]
        };
    },
    created() {}
};
</script>

<style lang="css">
</style>



