<template lang="html">
  <div class="page-container">
      <ds pt2="" desc=""/>
      <ds pt3="" desc="" />
    
    <demo title="" desc="">
        <page form
        :curr-page="pageCfg.pageIndex"
        :page-len="pageCfg.pageSize"
        :total-num="pageCfg.count" @change-page="changePage" slot="source" class="clearfix"></page>
      <code-snippet slot="code">
        &lt;page form
              :curr-page="pageCfg.pageIndex"
              :page-len="pageCfg.pageSize"
              :total-num="pageCfg.count" @change-page="changePage"&gt;&lt;/page&gt;
      </code-snippet>
    </demo>
    <api :attrs="attrs" :events="events" :methods="methods" />
  </div>
</template>

<script>
import Page from 'ct-adc-page';
import Markdown from 'common/markdown';
import CodeSnippet from 'common/code-snippet';
export default {
    components: {
        Page,
        Markdown,
        CodeSnippet
    },
    data() {
        return {
            pageCfg: {
                pageIndex: 1,
                count: 200,
                pageSize: 20
            },
            attrs: [
                {name: 'currPage', desc: '', type: 'Number', default: 1, all: '-'},
                {name: 'pageLen', desc: '', type: 'Number', default: 10, all: '-'},
                {name: 'totalNum', desc: '', type: 'Number', default: 1000, all: '-'},
                {name: 'form', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'spage', desc: '', type: 'Number', default: 5, all: '-'},
                {name: 'topPage', desc: '', type: 'Number', default: 1, all: '-'},
                {name: 'afterPage', desc: '', type: 'Number', default: 1, all: '-'}
            ],
            events: [
                {name: 'change-page', params: 'page, ', desc: ''}
            ],
            methods: [
                {name: 'setPage', params: 'page', desc: '', rt: '-'}
            ]
        };
    },
    methods: {
        changePage(page) {
            this.pageCfg.pageIndex = page;
        }
    }
};
</script>

<style lang="css">
</style>



