<template lang="html">
<div class="page-container">
    <ds pt2="Button " desc="" />

    <demo title="" desc="">
        <div slot="source">
            <div>
                <button class="btn btn-default"></button>
                <button class="btn btn-primary"></button>
                <button class="btn btn-success"></button>
                <button class="btn btn-warning"></button>
                <button class="btn btn-danger"></button>
            </div>
        </div>
    </demo>

    <demo title="" desc="">
        <div slot="source">
            <div>
                <button class="btn btn-primary btn-xs"> .btn-xs</button>
                <button class="btn btn-primary btn-sm"> .btn-sm</button>
                <button class="btn btn-primary"></button>
                <button class="btn btn-primary btn-lg"> .btn-lg</button>
            </div>
        </div>
    </demo>

    <demo title="">
        <p slot="desc"><a href="http://glyphicons.com/">glyphicons</a></p>
        <div slot="source">
            <div>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-plus"></i> </button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-search"></i> </button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-import"></i> </button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-export"></i> </button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-edit"></i> </button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-refresh"></i> </button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-download-alt"></i> </button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-save"></i> </button>
            </div>
            <br>
            <div>
                <button class="btn btn-info"><i class="glyphicon glyphicon-list-alt"></i> </button>
            </div>
            <br>
            <div>
                <button class="btn btn-success"><i class="glyphicon glyphicon-check"></i> </button>
                <button class="btn btn-success"><i class="glyphicon glyphicon-check"></i> </button>
            </div>
            <br>
            <div>
                <button class="btn btn-warning"><i class="glyphicon glyphicon-ban-circle"></i> </button>
                <button class="btn btn-warning"><i class="glyphicon glyphicon-ban-circle"></i> </button>
            </div>
            <br>
            <div>
                <button class="btn btn-danger"><i class="glyphicon glyphicon-remove"></i> </button>
            </div>
            <br>
            <div>
                <button class="btn btn-default"><i class="glyphicon glyphicon-chevron-left"></i></button>
                <button class="btn btn-default"></button>
            </div>
            <br>
            <div>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-refresh"></i></button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-zoom-in"></i></button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-plus"></i></button>
                <button class="btn btn-primary"><i class="glyphicon glyphicon-th-list"></i></button>
                <button class="btn btn-default"><i class="glyphicon glyphicon-chevron-left"></i></button>
                <!-- <button class="btn btn-danger"><i class="glyphicon glyphicon-off"></i></button> -->
            </div>
        </div>
    </demo>

    <demo title="" desc="">
        <div slot="source">
            <div>
                <button class="btn btn-primary" disabled><i class="glyphicon glyphicon-refresh rotate"></i> </button>
            </div>
        </div>
    </demo>
</div>
</template>
<script>
export default {
    mounted() {
        const {hljs} = window;

        $('pre code').each(function(i, block) {
            hljs.highlightBlock(block);
        });

        new window.Clipboard(document.querySelectorAll('[data-clipboard-text]')).on('success', function(e) {
            new MiniMsg({content: ''}).animation();
            e.clearSelection();
        });
    }
};
</script>
<style lang="css">
</style>



