<template lang="html">
    <div class="page-container">

        <ds pt2="" desc=""/>

        <demo title="" desc="">
            <adc-table :data="list.data" :status="list.status" :msg="list.msg" :loading="list.loading" slot="source">
                <adc-table-column prop="Id" name="ID"></adc-table-column>
                <adc-table-column prop="Name" name="Name"></adc-table-column>
                <adc-table-column prop="Data" name="Data"></adc-table-column>
                <adc-table-column prop="Status" name="Status"></adc-table-column>
            </adc-table>
            <code-snippet slot="code">
                &lt;adc-table :data="list.data" :status="list.status" :msg="list.msg" :loading="list.loading"&gt;
                    &lt;adc-table-column prop="Id" name="ID"&gt;&lt;/adc-table-column&gt;
                    &lt;adc-table-column prop="Name" name="Name"&gt;&lt;/adc-table-column&gt;
                    &lt;adc-table-column prop="Data" name="Data"&gt;&lt;/adc-table-column&gt;
                    &lt;adc-table-column prop="Status" name="Status"&gt;&lt;/adc-table-column&gt;
                &lt;/adc-table&gt;
                &lt;script&gt;
                    import List from 'ct-adc-list';
                    export default {
                        components: {
                            'adc-table': List['adc-table'],
                            'adc-table-column': List['adc-table-column'],
                            Markdown,
                            CodeSnippet
                        },
                        data() {
                            return {
                                list: {
                                    data: [
                                        { Id: 1, Name: '1', Data: '', Status: 1 },
                                        { Id: 2, Name: '2', Data: '', Status: 2 },
                                        { Id: 3, Name: '3', Data: '', Status: 3 }
                                    ],
                                    status: true,
                                    msg: '',
                                    loading: false
                                }
                            };
                        }
                    };
                &lt;script&gt;
            </code-snippet>
        </demo>

        <demo title="" desc="">
            <adc-table :data="list1.data" :status="list1.status" :msg="list1.msg" :loading="list1.loading" slot="source">
                    <adc-table-column prop="Id" name="ID"></adc-table-column>
                    <adc-table-column prop="Name" name="Name"></adc-table-column>
                    <adc-table-column prop="Data" name="Data"></adc-table-column>
                    <adc-table-column prop="Status" name="Status"></adc-table-column>
                </adc-table>
            <code-snippet slot="code">
                    &lt;adc-table :data="list.data" :status="list.status" :msg="list.msg" :loading="list.loading"&gt;
                        &lt;adc-table-column prop="Id" name="ID"&gt;&lt;/adc-table-column&gt;
                        &lt;adc-table-column prop="Name" name="Name"&gt;&lt;/adc-table-column&gt;
                        &lt;adc-table-column prop="Data" name="Data"&gt;&lt;/adc-table-column&gt;
                        &lt;adc-table-column prop="Status" name="Status"&gt;&lt;/adc-table-column&gt;
                    &lt;/adc-table&gt;
                    &lt;script&gt;
                        import List from 'ct-adc-list';
                        export default {
                            components: {
                                'adc-table': List['adc-table'],
                                'adc-table-column': List['adc-table-column'],
                                Markdown,
                                CodeSnippet
                            },
                            data() {
                                return {
                                    list1: {
                                        data: [],
                                        status: true,
                                        msg: '',
                                        loading: false
                                    }
                                };
                            }
                        };
                    &lt;script&gt;
                </code-snippet>
        </demo>

        <demo title="" desc="">
            <adc-table :data="list2.data" :status="list2.status" :msg="list2.msg" :loading="list2.loading" slot="source">
                    <adc-table-column prop="Id" name="ID"></adc-table-column>
                    <adc-table-column prop="Name" name="Name"></adc-table-column>
                    <adc-table-column prop="Data" name="Data"></adc-table-column>
                    <adc-table-column prop="Status" name="Status"></adc-table-column>
                    <adc-table-column name="" :vm="list2.vm.operation" width="100"></adc-table-column>
                </adc-table>
            <code-snippet slot="code">
                    &lt;adc-table :data="list.data" :status="list.status" :msg="list.msg" :loading="list.loading"&gt;
                        &lt;adc-table-column prop="Id" name="ID"&gt;&lt;/adc-table-column&gt;
                        &lt;adc-table-column prop="Name" name="Name"&gt;&lt;/adc-table-column&gt;
                        &lt;adc-table-column prop="Data" name="Data"&gt;&lt;/adc-table-column&gt;
                        &lt;adc-table-column prop="Status" name="Status"&gt;&lt;/adc-table-column&gt;
                        &lt;adc-table-column name="" width="100"&gt;&lt;/adc-table-column&gt;
                    &lt;/adc-table&gt;
                    &lt;script&gt;
                        import List from 'ct-adc-list';
                        export default {
                            components: {
                                'adc-table': List['adc-table'],
                                'adc-table-column': List['adc-table-column'],
                                Markdown,
                                CodeSnippet
                            },
                            data() {
                                return {
                                    list2: {
                                        data: [
                                            { Id: 1, Name: '1', Data: '', Status: 1 },
                                            { Id: 2, Name: '2', Data: '', Status: 2 },
                                            { Id: 3, Name: '3', Data: '', Status: 3 }
                                        ],
                                        status: true,
                                        msg: '',
                                        loading: false
                                    }
                                };
                            }
                        };
                    &lt;script&gt;
                </code-snippet>
        </demo>

        <api name="Table" :attrs="attrs1" :methods="methods1"/>
        <api name="Table-column" :attrs="attrs2" />
    </div>
</template>

<script>
import Vue from 'vue';
import List from 'ct-adc-list';
import Markdown from 'common/markdown';
import CodeSnippet from 'common/code-snippet';
import button2 from '../components/table-button';

Vue.component('button2', button2);

export default {
    components: {
        'adc-table': List['adc-table'],
        'adc-table-column': List['adc-table-column'],
        Markdown,
        CodeSnippet
    },
    data() {
        return {
            btn1: true,
            list: {
                data: [
                    { Id: 1, Name: '1', Data: '', Status: 1 },
                    { Id: 2, Name: '2', Data: '', Status: 2 },
                    { Id: 3, Name: '3', Data: '', Status: 3 }
                ],
                status: true,
                msg: '',
                loading: false
            },
            list1: {
                data: [],
                status: true,
                msg: '',
                loading: false
            },
            list2: {
                data: [
                    { Id: 1, Name: '1', Data: '', Status: 1 },
                    { Id: 2, Name: '2', Data: '', Status: 2 },
                    { Id: 3, Name: '3', Data: '', Status: 3 }
                ],
                status: true,
                msg: '',
                loading: false,
                vm: {
                    operation: {
                        name: 'button2'
                    }
                }
            },
            methods1: [{
                name: 'getChecked',
                params: '-',
                desc: '',
                rt: '{allChecked: true, checked: []}'
            }],
            attrs1: [{
                name: 'data',
                desc: '',
                type: 'Array',
                default: '[]',
                all: '-'
            }, {
                name: 'initialAllChecked',
                desc: ' (typeselection)',
                type: 'Boolean',
                default: false,
                all: '-'
            }, {
                name: 'initialChecked',
                desc: ' (typeselection)',
                type: 'Array',
                default: '[]',
                all: '-'
            }, {
                name: 'loading',
                desc: '',
                type: 'Boolean',
                default: false,
                all: '-'
            }, {
                name: 'status',
                desc: '(/)',
                type: 'Boolean',
                default: true,
                all: '-'
            }, {
                name: 'msg',
                desc: '(statustrue(""),statusfalse(""))',
                type: 'String',
                default: '""',
                all: '-'
            }],
            attrs2: [{
                name: 'type',
                desc: '',
                type: 'String',
                default: '-',
                all: '-'
            }, {
                name: 'prop',
                desc: '',
                type: 'String',
                default: '-',
                all: '-'
            }, {
                name: 'name',
                desc: '',
                type: 'String',
                default: '-',
                all: '-'
            }, {
                name: 'width',
                desc: '',
                type: 'String',
                default: '-',
                all: '-'
            }, {
                name: 'vm',
                desc: '',
                type: 'plain Object',
                default: '{name:"", config:{}}',
                all: '-'
            }, {
                name: 'visible',
                desc: ',function',
                type: 'Boolean/Function',
                default: true,
                all: '-'
            }, {
                name: 'filter',
                desc: '(Vuefilters,function',
                type: 'String/Function',
                default: '-',
                all: '-'
            }, {
                name: 'className',
                desc: 'tdclass(function)',
                type: 'String/Function/Array',
                default: '-',
                all: '-'
            }, {
                name: 'mapper',
                desc: '((1""))',
                type: 'Array',
                default: '[]',
                all: '-'
            }, {
                name: 'ashtml',
                desc: 'html',
                type: 'Boolean',
                default: false,
                all: '-'
            }]
        };
    }
};
</script>



