<template>
    <div class="page-container">
        <ds pt2="textarea" desc="textarea" />
        <ds pt3="" desc="textarea" />
        <demo title="" desc="textarea">
            <ta slot="source" />
            <code-snippet slot="code">
                &lt;ta slot="source" /&gt;
                &lt;script&gt;
                    import ta from 'ct-adc-textarea';

                    export default {
                        components: {
                            ta
                        }
                    };
                    &lt;/script&gt;
            </code-snippet>
        </demo>

        <api :attrs="attrs" :events="events" :methods="methods" />
    </div>
</template>
<script>
import ta from 'ct-adc-textarea';

export default {
    data(){
        return {
            dataSource1: [{
                name: '',
                age: 20
            }, {
                name: '',
                age: 21
            }],
            attrs: [
                {name: 'maxLength', desc: '', type: 'Number', default: '-1', all: '-'},
                {name: 'cut', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'byByte', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'regexp', desc: '', type: 'RegExp', default: 'undefined', all: '-'},
                {name: 'replaceByEmpty', desc: '', type: 'Boolean', default: false, all: '-'}
            ],
            events: [
                {name: '-', desc: '-', params: '-'}
            ],
            methods: [
                {name: 'isPass', desc: '', params: '-', rt: '-'}
            ]
        };
    },
    components: {
        ta
    }
};
</script>



