<template lang="html">
  <div class="page-container">
    <ds pt2="" desc="" />
    <ds pt3="" desc="" />
    <demo title="" desc="keyididname;">
        <auto-complete style="width:300px;" slot="source" placeholder="ID" :list="aclist" :keys="['Name', 'Id', 'Code']" :matchKeys="['Name', 'Id', 'Code']" :showKeys="['Name', 'Id', 'Code']" @change="setScopeId" autoClear></auto-complete>
        <code-snippet slot="code">
            &lt;auto-complete placeholder="ID" :list="aclist"
                :keys="['Name', 'Id', 'Code']" :matchKeys="['Name', 'Id', 'Code']"
                :showKeys="['Name', 'Id', 'Code']" @change="setScopeId" autoClear&gt;&lt;/auto-complete&gt;

            &lt;script&gt;
                import AutoComplete from 'ct-adc-auto-complete';
                export default {
                    data() {
                        return {
                            aclist: [
                                {Name: 'Name1', Id: '1', Code: '1'},
                                {Name: 'Name2', Id: '2', Code: '2'},
                                {Name: 'Name3', Id: '3', Code: '3'},
                                {Name: 'Name4', Id: '4', Code: '4'},
                                {Name: 'Name5', Id: '5', Code: '5'},
                                {Name: 'Name6', Id: '6', Code: '6'}
                            ]
                        };
                    },
                    components: {
                        AutoComplete
                    },
                    methods: {
                        setScopeId() {}
                    }
                };
                &lt;/script&gt;
        </code-snippet>
    </demo>

    <demo title="" desc="">
            <auto-complete style="width:300px;" slot="source" caseInsensitive placeholder="ID" :list="aclist" :keys="['Name', 'Id', 'Code']" :matchKeys="['Name', 'Id', 'Code']" :showKeys="['Name', 'Id', 'Code']" @change="setScopeId" autoClear></auto-complete>
            <code-snippet slot="code">
                &lt;auto-complete caseInsensitive placeholder="ID" :list="aclist"
                    :keys="['Name', 'Id', 'Code']" :matchKeys="['Name', 'Id', 'Code']"
                    :showKeys="['Name', 'Id', 'Code']" @change="setScopeId" autoClear&gt;&lt;/auto-complete&gt;
    
                &lt;script&gt;
                    import AutoComplete from 'ct-adc-auto-complete';
                    export default {
                        data() {
                            return {
                                aclist: [
                                    {Name: 'Name1', Id: '1', Code: '1'},
                                    {Name: 'Name2', Id: '2', Code: '2'},
                                    {Name: 'Name3', Id: '3', Code: '3'},
                                    {Name: 'Name4', Id: '4', Code: '4'},
                                    {Name: 'Name5', Id: '5', Code: '5'},
                                    {Name: 'Name6', Id: '6', Code: '6'}
                                ]
                            };
                        },
                        components: {
                            AutoComplete
                        },
                        methods: {
                            setScopeId() {}
                        }
                    };
                    &lt;/script&gt;
            </code-snippet>
        </demo>

        <api :attrs="attrs" :events="events" :methods="methods" />
  </div>
</template>

<script>
import AutoComplete from 'ct-adc-auto-complete';
import Markdown from 'common/markdown';
import CodeSnippet from 'common/code-snippet';
export default {
    data() {
        return {
            aclist: [
                {Name: 'Name1', Id: '1', Code: '1'},
                {Name: 'Name2', Id: '2', Code: '2'},
                {Name: 'Name3', Id: '3', Code: '3'},
                {Name: 'Name4', Id: '4', Code: '4'},
                {Name: 'Name5', Id: '5', Code: '5'},
                {Name: 'Name6', Id: '6', Code: '6'}
            ],
            attrs: [
                {name: 'allForEmpty', desc: 'input', type: 'Boolean', default: true, all: '-'},
                {name: 'list', desc: '', type: 'Array', default: '[]', all: '-'},
                {name: 'keys', desc: 'key|', type: 'Array', default: '["Id","Name"]', all: '-'},
                {name: 'matchKeys', desc: 'key', type: 'Array', default: '["Id","Name"]', all: '-'},
                {name: 'showKeys', desc: 'inputkey', type: 'Array', default: '["Id","Name"]', all: '-'},
                {name: 'value', desc: 'keyautoClearfalse', type: 'Object', default: '{}', all: '-'},
                {name: 'placeholder', desc: 'placeholder', type: 'String', default: '...', all: '-'},
                {name: 'disabled', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'maxlength', desc: '', type: 'Number', default: 100000, all: '-'},
                {name: 'caseInsensitive', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'autoSelectIfOne', desc: '', type: 'Boolean', default: false, all: '-'}
            ],
            events: [
                {name: 'select', desc: 'select', params: 'Object'},
                {name: 'change', desc: '(selected)change""""', params: 'Object'},
                {name: 'clear', desc: 'X', params: '-'}
            ],
            methods: [
                {name: 'getValue', params: '-', rt: '', desc: ''}
            ]
        };
    },
    components: {
        AutoComplete,
        Markdown,
        CodeSnippet
    },
    methods: {
        setScopeId() {}
    }
};
</script>




