<template>
    <div class="page-container">
        <ds pt2="" desc="" />
        <ds pt3="" desc="" />
        <demo title="" desc="">
            <div style="width: 600px;" slot="source"><area-selector showNation /></div>
            <code-snippet slot="code">
                &lt;div style="width: 600px;" slot="source"&gt;&lt;area-selector showNation /&gt;&lt;/div/&gt;
                &lt;script&gt;
                    import AreaSelector from 'ct-adc-area-selector';

                    export default {
                        components: {
                            AreaSelector
                        }
                    };
                    &lt;/script&gt;
            </code-snippet>
        </demo>

        <api :attrs="attrs" :events="events" :methods="methods" />
    </div>
</template>
<script>
import AreaSelector from 'ct-adc-area-selector';

export default {
    data(){
        return {
            dataSource1: [{
                name: '',
                age: 20
            }, {
                name: '',
                age: 21
            }],
            attrs: [
                {name: 'initialArea', desc: '- --0601(-)', type: 'String', default: '-', all: '-'},
                {name: 'sep', desc: '', type: 'String', default: '-', all: '-'},
                {name: 'showNation', desc: 'selector', type: 'Boolean', default: false, all: '-'},
                {name: 'codeForNation', desc: '""code""', type: 'String', default: '00', all: '-'},
                {name: 'min', desc: 'province', type: 'String', default: 'region', all: '-'},
                {name: 'areaList', desc: '', type: 'Function', default: false, all: '-'}
            ],
            events: [
                {name: 'change', desc: 'select(sep) --', params: 'value1, value2'}
            ],
            methods: [
                {name: 'getArea', desc: '', params: 'readable', rt: ''}
            ]
        };
    },
    components: {
        AreaSelector
    }
};
</script>



