<template>
    <div class="page-container">
        <ds pt2="" desc="" />
        <ds pt3="" desc="" />
        <demo title="" desc="">
            <transfer slot="source" :dataSource="dataSource1" :showKey="['name']" :matchKey="['name', 'age']"/>
            <code-snippet slot="code">
                &lt;transfer slot="source" :dataSource="dataSource1" :showKey="['name']" :matchKey="['name', 'age']"/&gt;
                &lt;script&gt;
                    import transfer from 'ct-adc-transfers';

                    export default {
                        data(){
                            return {
                                dataSource1: [{
                                    name: '',
                                    age: 20
                                }, {
                                    name: '',
                                    age: 21
                                }]
                            };
                        },
                        components: {
                            transfer
                        }
                    };
                    &lt;/script&gt;
            </code-snippet>
        </demo>

        <api :attrs="attrs" :events="events" :methods="methods" />
    </div>
</template>
<script>
import transfer from 'ct-adc-transfers';

export default {
    data(){
        return {
            dataSource1: [{
                name: '',
                age: 20
            }, {
                name: '',
                age: 21
            }],
            attrs: [
                {name: 'dataSource', desc: '', type: 'Array', default: '[]', all: '-'},
                {name: 'selectedItems', desc: '[]dataSource(keymatchKey)', type: 'Array', default: '[]', all: '-'},
                {name: 'matchKey', desc: 'key', type: 'Array', default: '[]', all: '-'},
                {name: 'showKey', desc: 'keykey,"-"', type: 'Array', default: '["Name"]', all: '-'},
                {name: 'leftLoading', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'rightLoading', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'button', desc: 'html', type: 'Object', default: '', all: '-'},
                {name: 'leftListTitle', desc: 'title', type: 'String', default: '', all: '-'},
                {name: 'rightListTitle', desc: 'title', type: 'String', default: '', all: '-'}
            ],
            events: [
                {name: 'change', desc: '""/""', params: ''}
            ]
        };
    },
    components: {
        transfer
    }
};
</script>



