<template lang="html">
<div class="page-container">
    <ds pt2="" desc="" />
    <ds pt3="" desc="" />

    <demo title="" desc="">
          <dates slot="source" related :begin-ops="{dateFormat: 'yyyy-MM-dd'}" :end-ops="{dateFormat: 'yyyy-MM-dd'}" begin-placeholder="" end-placeholder="" ref="dates" @change="updateTime" :initialEndDate="EndDate" :initialBeginDate="BeginDate"></dates>
    <code-snippet slot="code">
    &lt;dates related :begin-ops="{dateFormat: 'yyyy-MM-dd'}" :end-ops="{dateFormat: 'yyyy-MM-dd'}"
      begin-placeholder="" end-placeholder="" ref="dates" @change="updateTime" :initialEndDate="EndDate" :initialBeginDate="BeginDate"&gt;&lt;/dates&gt;
      </code-snippet>
    </demo>
    
    <api name="Date" :attrs="attrs1" :events="events1" :methods="methods1" />
    <api name="Dates" :attrs="attrs2" :events="events2" :methods="methods2" />
</div>
</template>

<script>
import form from 'ct-adc-form';
import Markdown from 'common/markdown';
import CodeSnippet from 'common/code-snippet';
export default {
    components: {
        Markdown,
        CodeSnippet,
        dates: form.DatesInput
    },
    data() {
        return {
            EndDate: $.now(),
            BeginDate: $.now() - 30 * 24 * 36 * 1e5,
            attrs1: [
                {name: 'initialDate', desc: '', type: 'String/Number', default: '-', all: '-'},
                {name: 'disabled', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'ops', desc: '', type: 'Object', default: '{type:"date"...}', all: '-'}
            ],
            methods1: [
                {name: 'getDate', desc: '', params: 'readable false', rt: ''}
            ],
            methods2: [
                {name: 'getDate', desc: '', params: 'readable', rt: ''},
                {name: 'getDates', desc: '', params: 'readable,endTransfered', rt: '{begin: 123132, end: 123123123}'}
            ],
            attrs2: [
                {name: 'initialBeginDate', desc: '()', type: 'String/Number', default: '-', all: '-'},
                {name: 'beginPlaceholder', desc: 'placeholder', type: 'String', default: '', all: '-'},
                {name: 'beginOps', desc: '', type: 'Object', default: '{type:"date"...}', all: '-'},
                {name: 'beginDisabled', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'initialEndDate', desc: '()', type: 'String/Number', default: '-', all: '-'},
                {name: 'endPlaceholder', desc: 'placeholder', type: 'String', default: '', all: '-'},
                {name: 'endOps', desc: '', type: 'Object', default: '{type:"date"...}', all: '-'},
                {name: 'endDisabled', desc: '', type: 'Boolean', default: false, all: '-'},
                {name: 'related', desc: '', type: 'Boolean', default: false, all: '-'}
            ]
        };
    },
    methods: {
        updateTime(row) {
            console.log(row);
        }
    }
};
</script>



