<template>
    <div class="page-container">
        <h2 class="pt2"></h2>
        <p></p>
        <h3 class="pt3"></h3>
        <p></p>
        <h3 class="pt3"></h3>
        <p></p>
        <div class="demo">
            <div class="demo-source">
                <button class="btn btn-primary" @click="isShow1=true"></button>
                <button class="btn btn-primary" @click="isShow2=true"></button>
            </div>
            <div class="demo-code">
                <code-snippet>
                    &lt;slide-out v-model="isShow1" title="" width="60%" hide-on-blur is-transparent&gt;
                        &lt;div slot="body" class="form-horizontal form-horizontal-sm"&gt;
                            &lt;div class="form-group"&gt;
                                &lt;label class="col-sm-2 control-label"&gt;&lt;/label&gt;
                                &lt;div class="col-sm-4 form-control-static"&gt;clovemx12345677@gmail.com&lt;/div&gt;
                            &lt;/div&gt;
                            &lt;div class="form-group"&gt;
                                &lt;label class="col-sm-2 control-label"&gt;&lt;/label&gt;
                                &lt;div class="col-sm-4 form-control-static"&gt;123456&lt;/div&gt;
                            &lt;/div&gt;
                        &lt;/div&gt;
                    &lt;/slide-out&gt;
                    &lt;slide-out v-model="isShow2" title="" width="60%"&gt;
                        &lt;div slot="body" class="form-horizontal form-horizontal-sm"&gt;
                            &lt;div class="form-group"&gt;
                                &lt;label class="col-sm-2 control-label"&gt;&lt;/label&gt;
                                &lt;div class="col-sm-4"&gt;&lt;input type="text" class="form-control" placeholder=""&gt;&lt;/div&gt;
                            &lt;/div&gt;
                            &lt;div class="form-group"&gt;
                                &lt;label class="col-sm-2 control-label"&gt;&lt;/label&gt;
                                &lt;div class="col-sm-4"&gt;&lt;input type="text" class="form-control" placeholder=""&gt;&lt;/div&gt;
                            &lt;/div&gt;
                            &lt;div class="form-group"&gt;
                                &lt;div class="col-sm-offset-2 col-sm-6"&gt;
                                    &lt;button class="btn btn-primary" @click="save2"&gt;&lt;/button&gt;
                                    &lt;button class="btn btn-default" @click="isShow2=false"&gt;&lt;/button&gt;
                                &lt;/div&gt;
                            &lt;/div&gt;
                        &lt;/div&gt;
                    &lt;/slide-out&gt;
                    &lt;script&gt;
                        import SlideOut from 'ct-adc-slideout';
                        import CodeSnippet from 'common/code-snippet';

                        export default {
                            name: 'slideout',
                            data(){
                                return {
                                    isShow1: false,
                                    isShow2: false
                                };
                            },
                            components: {
                                SlideOut,
                                CodeSnippet
                            },
                            methods: {
                                save2(){
                                    this.isShow2 = false;
                                }
                            }
                        };
                    &lt;/script&gt;
                </code-snippet>
            </div>
        </div>
        <api :attrs="attrs" :events="events" :slots="slots" :methods="methods" />
        <slide-out v-model="isShow1" title="" width="60%" hide-on-blur is-transparent>
            <div slot="body" class="form-horizontal form-horizontal-sm">
                <div class="form-group">
                    <label class="col-sm-2 control-label"></label>
                    <div class="col-sm-4 form-control-static">clovemx12345677@gmail.com</div>
                </div>
                <div class="form-group">
                    <label class="col-sm-2 control-label"></label>
                    <div class="col-sm-4 form-control-static">123456</div>
                </div>
            </div>
        </slide-out>
        <slide-out v-model="isShow2" title="" width="60%">
            <div slot="body" class="form-horizontal form-horizontal-sm">
                <div class="form-group">
                    <label class="col-sm-2 control-label"></label>
                    <div class="col-sm-4"><input type="text" class="form-control" placeholder=""></div>
                </div>
                <div class="form-group">
                    <label class="col-sm-2 control-label"></label>
                    <div class="col-sm-4"><input type="text" class="form-control" placeholder=""></div>
                </div>
                <div class="form-group">
                    <div class="col-sm-offset-2 col-sm-6">
                        <button class="btn btn-primary" @click="save2"></button>
                        <button class="btn btn-default" @click="isShow2=false"></button>
                    </div>
                </div>
            </div>
        </slide-out>
    </div>
</template>

<script>
import SlideOut from 'ct-adc-slideout';
import CodeSnippet from 'common/code-snippet';
import api from 'common/api';

export default {
    name: 'slideout',
    data(){
        return {
            isShow1: false,
            isShow2: false,
            attrs: [{
                name: 'v-model',
                type: 'Boolean',
                desc: '',
                default: '-',
                all: '-'
            }, {
                name: 'z-index',
                type: 'Number',
                desc: '',
                default: 1000,
                all: '-'
            }, {

                name: 'title',
                type: 'String',
                desc: '',
                default: '',
                all: '-'
            }, {
                name: 'width',
                type: 'String',
                desc: '',
                default: '50%',
                all: '-'
            }, {
                name: 'hide-on-blur',
                type: 'Boolean',
                desc: '',
                default: false,
                all: '-'
            }, {
                name: 'is-transparent',
                type: 'Boolean',
                desc: '',
                default: false,
                all: '-'
            }],
            events: [{
                name: 'on-show',
                desc: '',
                params: '-'
            }, {
                name: 'on-hide',
                desc: '',
                params: '-'
            }, {
                name: 'on-after-show',
                desc: '',
                params: '-'
            }, {
                name: 'on-after-hide',
                desc: '',
                params: '-'
            }],
            slots: [{
                name: 'head',
                desc: 'slot'
            }, {
                name: 'body',
                desc: 'slot'
            }],
            methods: [{
                name: 'scrollTo',
                desc: '',
                params: 'axisY'
            }]
        };
    },
    components: {
        SlideOut,
        CodeSnippet,
        api
    },
    methods: {
        save2(){
            this.isShow2 = false;
        }
    }
};
</script>





